.CastGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(190px, 1fr));
  grid-gap: 16px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.CastGalleryItem {
  overflow: hidden;
  background-color: #1e90ff;
  background-size: cover;
  height: 244px;
  padding: 1px;
  border-radius: 4px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.CastGalleryItemImage {
  border: 1px solid #2d2727;
  display: block;
  vertical-align: top;
  height: 80%;
  min-width: 100%;
  background-size: cover;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.NameCast {
  text-align: center;
  color: #ffffff;
  font-size: 14px;
}

.CharacterCast {
  font-size: 12px;
  font-style: italic;
  padding-bottom: 4px;
}
